html *, html *:before, html *:after {
    box-sizing: inherit;
}


::selection {
    background: #b3d4fc;
    text-shadow: none;
}
body {
  font-family: "proxima-nova", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 1.6;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  display: flex;
  flex-direction: column;
  margin: 0;
  height: 100%;
  min-height: 100%;
}
.content {
    max-width: 100%;
    width: 900px;
    margin-left: auto;
    margin-right: auto;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
/* .mo {
    display: flex;
    align-items: flex-start;
} */

.mo__body {
    flex: 1;
    min-width: 0;
    position: relative;
}

.mo.o__centered {
    align-items: center;
}
.header__meta_wrapper {
    margin-bottom: 15px;
    line-height: 1;
}


.App-header {
  background-color: #282c34;
  min-height: 5vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  font-size: calc(5px + 1vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.header {
    color: white;
    padding: 35px 0;
    margin-bottom: 7px;
    flex: none;
    position: relative;
    z-index: 10;
    background-size: cover;
    box-shadow: 0 1px 4px 0 rgb(0 0 0 / 20%);
    background-position: 50%;
}
.header__lite .header__form.header__form {
    margin-left: auto;
    float: right;
}
.animation__header-lite .header__form.header__form, .header__lite .header__form.header__form {
    width: 100%;
}
.search {
    position: relative;
}
.animation__header-lite .search__input, .header__lite .search__input {
    padding-top: 16px;
    padding-bottom: 16px;
}

.search__input {
    width: 100%;
    padding: 20px 32px 21px 59px;
    background: rgba(255, 255, 255, 0.2);
    border: none;
    outline: none;
    color: rgba(255, 255, 255, 0.7);
    font-size: 18px;
    border-radius: 4px;
    box-shadow: 0 1px 2px 0 rgb(0 0 0 / 3%);
    transition: background .4s, box-shadow .2s;
}

.search__input:active, .search__input:focus {
    box-shadow: 0 10px 20px rgb(0 0 0 / 14%);
    background: white;
    color: #3a3c4c;
}

[type="text"], [type="email"], [type="number"], textarea {
    font-family: "proxima-nova", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
form {
    display: block;
    margin-top: 0em;
    width: 100%;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
